header{
    width: 100%; 
    
    .inner{
        width: 1180px; height: 80px; 
        margin: 0 auto; 
        position: relative; 

        h1{
            position: absolute; 
            bottom: 20px; left: 0; 
            a{
                font:bold 24px/1 arial; color: #fff;
            }
        }
        #gnb{
            position: absolute; 
            right: 0; bottom: 0; 

            li{float: left; 
                a{
                    display: inline-block; 
                    padding: 20px 30px; 
                    font:bold 14px/1 arial; 
                    color: #fff; 
                }
            }
        }
        
        .fa-bars{
            font-size: 24px; 
            position: absolute; 
            bottom: 20px; 
            right: 40px;
            display: none;
        }
    }

    &.sub{
        background: #222;

        .inner{
            h1 a{
                color: #fff; 
            }
            #gnb li a{
                color: #fff; 
            }
            .fa-bars{ color: #fff; }
        }
    }
    &.main{
        background: transparent;
        border-bottom: 1px solid #fff;
        position: absolute; 
        top: 0; left: 0; 
        z-index: 2;

        .inner{
            h1 a{
                color: #fff; 
            }
            #gnb li a{
                color: #fff;
            }
            .fa-bars{ color: #fff; }

        }
    }
}


@media screen and (max-width:$tablet){
    header{         
        .inner{
            width: 100%;  
                 
            h1{
                 left: 40px;                  
            }
            #gnb{
               display: none;
            }
            .fa-bars{ display: block; }
            
        } 
    } 
}